.menu-nav {}

.menu-nav__list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @media screen and (min-width: 768px) {
        flex-direction: row;
        gap: 28px;
    }
}

.menu-nav__item {}

.menu-nav__link {
    color: var(--color-black);

    @media screen and (min-width: 1280px) {
        color: var(--main-color-text);
    }

    &:hover,
    &:focus {
        color: var(--active-color);
    }
}